import { OrderService } from './../order.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {

  pickup_location :any = {}
  currLat: any;
  currLng: any;
  order: any = {
    weight : 5,
    price : 69
  };

  constructor(private orderService: OrderService) {
    this.getCurrentLocation();
   }

  ngOnInit() {
  }

  changeWeight(weight: number){
    this.order.weight = weight;
  }
  select(doc) {
    this.order.inside = doc;
    this.calulateShipping();
  }
  submit(f) {
    // this.order.details = f.value;
    // console.log(this.order);
    this.calulateShipping()
  }


  getCurrentLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {

        this.currLat = position.coords.latitude;
        this.currLng = position.coords.longitude;
      });
      console.log("longitude : " + this.currLng);
      console.log("Lattitude : " + this.currLat);
    }
    else {
      console.log("Geolocation is not supported by this browser.");
    }
  }

  calulateShipping() {

    this.orderService.calculatePrice().subscribe((d: any) => {
      this.order.price = d.distance as number * 8;
      if(this.order.weight > 5){
        switch(this.order.weight){
          case 10 : this.order.price += 50;
                    break;
          case 15 : this.order.price += 100;
                    break;
          case 20 : this.order.price += 150;
                    break;
        }
      }
      console.log(this.order.weight);
    });
  }

}

